.search-users {
    height: 2.3rem;
    background-color:rgb(240, 239, 239);
    border-radius: 10px;
    display: flex;
    align-items: center;
    padding: 0.5rem;
    width: 220px;
  .arrow {
    width: 0;
    height: 0;
    border-left: 10px solid transparent;
    border-right: 10px solid transparent;
    border-bottom: 12px solid rgb(253, 253, 253);
    // box-shadow: #80808038 0 0 18px 1px;
    filter: drop-shadow(0px -1px 0px #80808038);
    z-index: 5;
    // filter: drop-shadow(1px 1px 1px rgba(0, 0, 0, 0.5));
    position: absolute;
    left: 50%;
    transform: translate(-50%);
    top: 80%;
  }
  .search-input{
      background-color: rgb(240, 239, 239);
      border-radius: 10px;
      height: 2rem;
      display: flex;
      align-items: center;

      .search-icon-hide{
        display: none;
      }
  }
  input{
      border: none;
      background-color: inherit;
    //   height: 2rem;
    border-radius: 10px;
    font-size: 15px;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    &:focus{
        outline: none;
    }  
}
}

.search-result {
  position: absolute;
  top: 100%;
  height: 380px;
  width: 360px;
  background-color: rgb(253, 253, 253);
  left: 50%;
  border-radius: 5px;
  transform: translate(-50%);
  box-shadow: #80808038 0 0 18px 1px;
  padding-top: 1rem;
  overflow-y: auto;

  .search-user-result{
      display: flex;
      align-items: center;
      height: 3rem;
      gap: 1rem;
      padding: 1rem;
       
      &:hover{
          background-color: rgb(247, 247, 247);
      }

       img{
           height: 40px;
           width: 40px;
           object-fit: cover;
       }

      .search-username{
          display: flex;
          flex-direction: column;
          .search-main{
             font-weight: 600;
             font-size: 14px;
          }
          .search-second{
              font-size: 14px;
              color: grey;
          }
        //   justify-content: center;
      }
  }
}
